.productMain_container{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    padding: 20px;
}

.productMain_container_slider{
    width: 70%;
    margin: 10px auto;
    overflow: hidden;
    border: 2px solid #e1e1e1;
    border-radius: 20px;

    
}

.productMain_container_slider_slides{
    display: flex;
    flex-wrap: nowrap;
    transition: transform 0.3s ease-in-out;

    img{
        width: 100%;
    }
}

.gallery{
    width: 70%;
    margin: 0 auto;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(5,1fr);
    img{
        width: 100%;
        object-fit: cover;
        border-radius: 50%;
        border: 2px solid #e1e1e1;
        opacity: 0.7;
    }
}

.productMain_container_images{
    position: sticky;
    height: 1px;
    top: 10px;
    
}

body{
    min-height: 500vh;
}

.productMain_container_info{
    height: 2000px;
}